<template>
  <div class="dd1">
    <div
      class="con"
      v-for="(item, index) in RrivateRoomData"
      :key="index"
      @click="linkDetails()"
    >
      <router-link :to="`/community/privateDetails?rid=${item.rid}`">
      <span class="btn">{{ item.haveroom==1?'有房':'无房' }}</span>
      <span class="bt">{{ item.title }}</span>
      <div class="img">
        <img
          v-for="(v, i) in img[item.rid-1]"
          :key="i"
          class="img1"
          :src="require(`../assets/img/roomshow/${v}`)"
          alt=""
        />
      </div>
      <div class="d1">
        <div class="d2">
          <!-- <img src="../assets/img/3.jpg" alt="" /> -->
          <div class="s1">{{ item.collect==null?'0':item.collect }}人收藏</div>
        </div>
        <div class="s2">{{ item.updatetime }}小时前发布</div>
      </div>
      </router-link>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      RrivateRoomData: [],
      img:[]
    };
  },
  methods: {
    linkDetails() {
      //this.$router.push({ name: "privateDetails" });
    },
    getRrivateRoom(){
      this.axios.get('/room').then(res=>{
        res.data.result.forEach(item=>{
          if(item.photo){
            this.img[item.rid-1]=item.photo.split(',');
            /*item.photo.split(',').forEach(i=>{
              this.img=require(`../assets/img/roomshow/${i}`)
            })*/
          }
        })
          this.RrivateRoomData=res.data.result;
      })
    },
  },
  mounted(){
    this.getRrivateRoom();
  }
};
</script>
<style>
