  border-bottom: 1px solid #ccc;
}
.dd1 .con .btn {
  width: 50px;
  height: 25px;
  font-size: 10px;
  color: #fff;
  text-align: center;
  border-radius: 5px;
  line-height: 25px;
  background-color: #007d65;
  float: left;
  display: block;
  margin-top: 3px;
}
.dd1 .con .d1 {
  display: flex;
}
.dd1 .con .d2 {
  display: flex;
  width: 82px;
  /* background: url("../assets/imgs/性别女.png") no-repeat right; */
}
.dd1 .con .d1 img {
  width: 25px;
  height: 25px;
  border-radius: 50%;
  margin-top: 3px;
}
.dd1 .con .d1 .s1 {
  line-height: 31px;
  margin-left: 10px;
  margin-top: 0px;
  color: #007d65;
}
.dd1 .con .d1 .s2 {
  line-height: 31px;
  font-size: 15px;
  position: absolute;
  right: 13px;
}
.dd1 .con .bt {
  color: #000;
  font-size: 18px;
  font-weight: 400;
  display: block;
  margin-top: 10px;
  margin-left: 57px;
  display: block;
  width: 300px;
}
.dd1 .con .img {
  text-align: center;
  margin: 10px 0;
}
.dd1 .con .img img {
  width: 100px;
  height: 80px;
  border-radius: 10px;
}
