<script>
export default {
	props:{
		loginRoute:String
	},
  mounted() {
	
	//   js效果
    let vm = this;
    const body = this.$refs.footerbar;
    // console.log(this.$router);
    const bgColorsBody = [
      "#ffb457",
      "#ff96bd",
      "#9999fb",
      "#ffe797",
      "#cffff1",
    ];
    const menu = body.querySelector(".menu");
    const menuItems = menu.querySelectorAll(".menu__item");
    const menuBorder = menu.querySelector(".menu__border");
    let activeItem = menu.querySelector(".active");

    // 点击后跳转的函数
    function clickItem(item, index, rter) {
      switch (index) {
        case 0:

          if (vm.$route.name !== "home") {
            rter.push("home");
          }
          break;
        case 1:
         	
          if (vm.$route.name !== "Renting") {
            rter.push("Renting");
          }
          break;
        case 2:
        	
          if (vm.$route.name !== "community") {
            rter.push("community");
          }
          break;
        case 3:
         	
          if (vm.$route.name !== "message") {
            rter.push("message");
          }
          break;
        case 4:
          	
          if (vm.$route.name !== vm.loginRoute) {
            rter.push(vm.loginRoute);
          }
          break;
        default:
          break;
      }

      
      menu.style.removeProperty("--timeOut");

      if (activeItem == item) return;
      

      if (activeItem) {
        activeItem.classList.remove("active");
      }

      item.classList.add("active");
      //   body.style.backgroundColor = bgColorsBody[index];
      activeItem = item;
      offsetMenuBorder(activeItem, menuBorder);
    }

    function offsetMenuBorder(element, menuBorder) {
      const offsetActiveItem = element.getBoundingClientRect();
      const left =
        Math.floor(
          offsetActiveItem.left -
            menu.offsetLeft -
            (menuBorder.offsetWidth - offsetActiveItem.width) / 2
        ) + "px";
      menuBorder.style.transform = `translate3d(${left}, 0 , 0)`;
    }

    offsetMenuBorder(activeItem, menuBorder);

    menuItems.forEach((item, index) => {
      var rter = this.$router;
      item.addEventListener("click", () => clickItem(item, index, rter));
    });

    window.addEventListener("resize", () => {
      offsetMenuBorder(activeItem, menuBorder);
      menu.style.setProperty("--timeOut", "none");
    });
  },
};
</script>
<style >
html {
  box-sizing: border-box;
  --bgColorMenu: #035e4c;
  --duration: 0.7s;
}

html *,
html *::before,
html *::after {
  box-sizing: inherit;
}

.footerbar {
  margin: 0;
  display: flex;
  height: 80px;
  overflow: hidden;
  align-items: center;
  justify-content: center;
  /* background-color: rgb(240, 66, 66); */
  -webkit-tap-highlight-color: transparent;
  transition: background-color var(--duration);
  width: 100%;
}

 .menu {
  margin: 0;
  display: flex;
  /* Works well with 100% width  */
  /* width: 32.05em; */
  width: 100%;
  font-size: 1.5em;
  padding: 0 2.85em;
  position: relative;
  align-items: center;
  justify-content: center;
  background-color: #fff;
  bottom: -14px;
}

 .menu__item {
  all: unset;
  flex-grow: 1;
  z-index: 100;
  display: flex;
  cursor: pointer;
  position: relative;
  border-radius: 50%;
  align-items: center;
  will-change: transform;
  justify-content: center;
  padding: 0.55em 0 0.85em;
  transition: transform var(--timeOut, var(--duration));
}

 .menu__item::before {
  content: "";
  z-index: -1;
  width: 4.2em;
  height: 4.2em;
  border-radius: 50%;
  position: absolute;
  transform: scale(0);
  transition: background-color var(--duration), transform var(--duration);
}

 .menu__item.active {
  transform: translate3d(0, -0.8em, 0);
}

 .menu__item.active::before {
  transform: scale(1);
  background-color: var(--bgColorItem);
}

 .icon {
  width: 2.6em;
  height: 2.6em;
  stroke: white;
  fill: transparent;
  stroke-width: 1pt;
  stroke-miterlimit: 10;
  stroke-linecap: round;
  stroke-linejoin: round;
  stroke-dasharray: 400;
}

 .menu__item.active .icon {
  animation: strok 1.5s reverse;
}

@keyframes strok {
  100% {
    stroke-dashoffset: 400;
  }
}

 .menu__border {
  left: 0;
  bottom: 99%;
  width: 10.9em;
  height: 2.4em;
  position: absolute;
  clip-path: url(#menu);
  will-change: transform;
  background-color: #fff;
  transition: transform var(--timeOut, var(--duration));
}

 .svg-container {
  width: 0;
  height: 0;
}

@media screen and (max-width: 50em) {
  .menu {
    font-size: 0.8em;
  }
}

</style>