<template>
  <div class="Renting">
    <!-- 搜索组件 -->
    <Search></Search>
    <section class="alltag">
      <!-- 功能标签开始 -->

      <mt-tabbar v-model="selected" class="tagbtn1">
        <mt-tab-item id="tab1" @click.native="linkTag(0)">
          <img slot="icon" src="../assets/img/tagbtn/longrenting.png" />
          长租
        </mt-tab-item>
        <mt-tab-item id="tab2" @click.native="linkTag(0)">
          <img slot="icon" src="../assets/img/tagbtn/room2.png" />
          短租
        </mt-tab-item>
        <mt-tab-item id="tab3" @click.native="linkTag(0)">
          <img slot="icon" src="../assets/img/tagbtn/bill.png" />
          订单
        </mt-tab-item>
        <mt-tab-item id="tab4" @click.native="linkTag(0)">
          <img slot="icon" src="../assets/img/tagbtn/message.png" />
          咨询
        </mt-tab-item>
      </mt-tabbar>

      <mt-tabbar v-model="selected" class="tagbtn2">
        <mt-tab-item id="tab5" @click.native="linkTag(0)">
          <img slot="icon" src="../assets/img/tagbtn/roomx.png" />
          公寓
        </mt-tab-item>
        <mt-tab-item id="tab6" @click.native="linkTag(0)">
          <img slot="icon" src="../assets/img/tagbtn/room1.png" />
          单租
        </mt-tab-item>
        <mt-tab-item id="tab7" @click.native="linkTag(0)">
          <img slot="icon" src="../assets/img/tagbtn/goodroom.png" />
          优选
        </mt-tab-item>
        <mt-tab-item id="tab8" @click.native="linkTag(0)">
          <img slot="icon" src="../assets/img/tagbtn/roomserve.png" />
          托管
        </mt-tab-item>
      </mt-tabbar>
      <!-- 功能标签结束 -->
    </section>
    <div class="content"></div>

    <!--筛选-->
    <div class="filter">
      <div class="box">
        <button
          @click="open($event)"
          :style="isto ? 'color:red' : 'color:black'"
        >
          {{ toLocation }}
        </button>
        <button
          @click="open1($event)"
          :style="isto1 ? 'color:red' : 'color:black'"
        >
          {{ toWay }}
        </button>
        <button @click="open2" :style="isto2 ? 'color:red' : 'color:black'">
          {{ price }}
        </button>
        <button @click="open3" :style="isto3 ? 'color:red' : 'color:black'">
          筛选
        </button>
        <button @click="open4">↑↓</button>
      </div>
      <transition name="van-fade">
        <div v-show="isto == true" class="pupop">
          <ul>
            <li
              @click="changeColor($event)"
              v-for="(item, index) of location"
              :key="index"
            >
              {{ item }}
            </li>
          </ul>
          <ul>
            <li
              @click="pquyu($event)"
              v-for="(item, index) of quyu"
              :key="index"
            >
              {{ item }}
            </li>
          </ul>
          <ul>
            <li
              v-show="isquyu == true"
              v-for="(item, index) of smallloction"
              :key="index"
              @click="setEvent($event)"
            >
              {{ item }}
            </li>
          </ul>
        </div>
      </transition>
      <!---->
      <transition name="van-fade">
        <div v-show="isto1 == true" class="way">
          <h3>整租</h3>
          <div>
            <van-tag
              plain
              @click="rent"
              :type="isrent ? 'danger' : 'default'"
              size="large"
              >不限</van-tag
            >
            <van-tag
              plain
              @click="rent1"
              :type="isrent1 ? 'danger' : 'default'"
              size="large"
              >一居</van-tag
            >
            <van-tag
              plain
              @click="rent2"
              :type="isrent2 ? 'danger' : 'default'"
              size="large"
              >二居</van-tag
            >
            <van-tag
              plain
              @click="rent3"
              :type="isrent3 ? 'danger' : 'default'"
              size="large"
              >三居</van-tag
            >
            <van-tag
              plain
              @click="rent4"
              :type="isrent4 ? 'danger' : 'default'"
              size="large"
              >四居+</van-tag
            >
          </div>

          <h3>合租</h3>
          <div>
            <van-tag
              plain
              @click="hrent"
              :type="ishrent ? 'danger' : 'default'"
              size="large"
              >不限</van-tag
            >
            <van-tag
              plain
              @click="hrent1"
              :type="ishrent1 ? 'danger' : 'default'"
              size="large"
              >一居</van-tag
            >
            <van-tag
              plain
              @click="hrent2"
              :type="ishrent2 ? 'danger' : 'default'"
              size="large"
              >二居</van-tag
            >
            <van-tag
              plain
              @click="hrent3"
              :type="ishrent3 ? 'danger' : 'default'"
              size="large"
              >三居</van-tag
            >
            <van-tag
              plain
              @click="hrent4"
              :type="ishrent4 ? 'danger' : 'default'"
              size="large"
              >四居+</van-tag
            >
          </div>
          <van-divider />
          <div class="bottom">
            <button @click="reset">重置</button>
            <button @click="define">确定</button>
          </div>
        </div>
      </transition>
      <!---->
      <transition name="van-fade">
        <div v-show="isto2 == true" class="price">
          <div class="box">
            <div>
              <span
                @click="price0"
                :style="isprice ? 'color:red' : 'color:black'"
                >不限</span
              >
            </div>
            <div>
              <span
                @click="price1"
                :style="isprice1 ? 'color:red' : 'color:black'"
                >1500元以下</span
              >
            </div>
            <div>
              <span
                @click="price2"
                :style="isprice2 ? 'color:red' : 'color:black'"
                >1500元-2500元</span
              >
            </div>
            <div>
              <span
                @click="price3"
                :style="isprice3 ? 'color:red' : 'color:black'"
                >2500元-4000元</span
              >
            </div>
            <div>
              <span
                @click="price4"
                :style="isprice4 ? 'color:red' : 'color:black'"
                >4000元-5500元</span
              >
            </div>
            <div>
              <span
                @click="price5"
                :style="isprice5 ? 'color:red' : 'color:black'"
                >5500元-7000元</span
              >
            </div>
            <div>
              <span
                @click="price6"
                :style="isprice6 ? 'color:red' : 'color:black'"
                >7000元以上</span
              >
            </div>
          </div>
          <button @click="reset1">重置</button> <button>确定</button>
        </div>
      </transition>
      <transition name="van-fade">
        <div v-show="isto3 == true" class="check">
          <h2>朝向</h2>
          <div class="direction">
            <button
              v-for="(item, index) of directionArr"
              :key="index"
              @click="tap(index)"
              :class="activeIndex == index ? 'active' : ''"
            >
              {{ item }}
            </button>
          </div>
          <h2>面积</h2>
          <div class="direction">
            <button
              v-for="(item, index) of areaArr"
              :key="index"
              @click="tap1(index)"
              :class="areaIndex == index ? 'active' : ''"
            >
              {{ item }}
            </button>
          </div>
          <h2>租约</h2>
          <div class="direction">
            <button
              v-for="(item, index) of contractArr"
              :key="index"
              @click="tap2(index)"
              :class="contractIndex == index ? 'active' : ''"
            >
              {{ item }}
            </button>
          </div>
          <h2>最早入住日</h2>
          <div class="direction">
            <button
              v-for="(item, index) of dateArr"
              :key="index"
              @click="tap3(index)"
              :class="dateIndex == index ? 'active' : ''"
            >
              {{ item }}
            </button>
          </div>
          <h2>最早入住日</h2>
          <div class="direction">
            <button
              v-for="(item, index) of originArr"
              :key="index"
              @click="tap4(index)"
              :class="originIndex == index ? 'active' : ''"
            >
              {{ item }}
            </button>
          </div>
          <div class="direction">
            <button @click="checkRest">重置</button> <button>确定</button>
          </div>
        </div>
      </transition>
      <transition name="van-fade">
        <div v-show="isto4 == true" class="changeweys">
          <div class="boxs">
            <div
              v-for="(item, index) of changeArr"
              :key="index"
              @click="tap5(index)"
              :class="changeIndex == index ? 'active' : ''"
            >
              {{ item }}
            </div>
          </div>
        </div>
      </transition>
    </div>

    <!---->

    <ZiRoom @click.native="linkRoom" v-for="(item,i) in houseData" :key="i" :item="item" @childItem="childItem"></ZiRoom>
    <router-view></router-view>
  </div>
</template>
<script>
import { mapMutations } from "vuex";
import Search from "../components/Search";
import ZiRoom from "../components/ZiRoom";

export default {
  components: {
    Search,
    ZiRoom,
  },
  data() {
    return {
      details:{},
      hid: '',
      houseData: [],
      selected: "",
      contentDetails: [
        {
          title: "合租.胜古北里-主卧",
          area: "2室1厅1卫 | 面积81 |南北距离通州北苑站444m",
          price: " ¥3000 /月",
        },
      ],
      changeArr: [
        "默认排序",
        "租金从大到小",
        "租金从小到大",
        "面积从大到小",
        "面积从小到大",
      ],
      changeIndex: 0,
      originIndex: -1,
      originArr: [
        "近地铁",
        "独立阳台",
        "可月付",
        "集中供暖",
        "智能锁",
        "VR看房",
        "电梯房",
        "独卫",
      ],
      dateArr: [
        "立即入住",
        "03/27",
        "03/28",
        "03/29",
        "03/30",
        "03/31",
        "04/01",
        "04/02",
        "04/03",
        "04/04",
        "04/05",
        "04/06",
        "04/07",
        "04/08",
        "04/09",
        "04/10",
      ],
      dateIndex: -1,
      contractArr: ["3个月内", "年租", "半年租"],
      contractIndex: -1,
      areaIndex: -1,
      areaArr: ["10以下", "10-20", "90以上", "40-60", "60-90", "20-40"],
      activeIndex: -1,
      directionArr: ["东", "南", "西", "北", "东北", "西北", "西南", "东南"],
      isprice: false,
      isprice1: false,
      isprice2: false,
      isprice3: false,
      isprice4: false,
      isprice5: false,
      isprice6: false,
      price: "价格",
      //
      ischange: false,
      ishrent: false,
      ishrent1: false,
      ishrent2: false,
      ishrent3: false,
      ishrent4: false,
      isrent: false,
      isrent1: false,
      isrent2: false,
      isrent3: false,
      isrent4: false,
      toWay: "方式",
      toLocation: "位置",
      isquyu: false,
      isto: false,
      isto1: false,
      isto2: false,
      isto3: false,
      isto4: false,
      location: ["区域", "地铁", "附近"],
      quyu: [
        "不限",
        "西城区",
        "东城区",
        "朝阳区",
        "石景山区",
        "海淀区",
        "丰台区",
        "房山区",
        "通州区",
        "门头沟区",
        "大兴区",
        "平谷区",
      ],
      smallloction: [
        "不限",
        "CBD",
        "安定门",
        "安贞",
        "奥林匹克公园",
        "北大",
        "学子湾",
      ],
    };
  },
  methods: {
     linkTag(i) {
      switch (i) {
        case 1:
          this.$router.push("/Renting");
          break;
        case 2:
          this.$router.push("/community");
          break;
        case 3:
          this.$router.push("/login");
          break;
        case 4:
          this.$router.push("/message");
          break;
        default: 

           this.$toast({
          message: "正在开发，敬请期待",
          // position: 'bottom',
          duration: 2000,
        });
          break
      }
    },
    childItem(val){
      this.details=val;
      this.hid=val.hid;
    },
    loadNavItems(){
      this.axios.get('/house').then(res=>{
        res.data.results.forEach(item=>{
          if(item.house_photo){
            item.image=require(`../assets/img/roomshow/${item.house_photo}`)
          }
        })
        this.houseData=res.data.results;
      })
    },
    linkRoom() {
      this.$router.push({
        name: "roomDetails",
        params: { parent: "Renting" ,hid: this.hid, details:this.details},
      });
    },
    ...mapMutations(["setActiveNews"]),
    define() {},
    checkRest() {
      this.originIndex = "";
      this.dateIndex = "";
      this.contractIndex = "";
      this.areaArr = "";
      this.activeIndex = "";
    },
    tap(index) {
      this.activeIndex = index;
    },
    tap1(index) {
      this.areaIndex = index;
    },
    tap2(index) {
      this.contractIndex = index;
    },
    tap3(index) {
      this.dateIndex = index;
    },
    tap4(index) {
      this.originIndex = index;
    },
    tap5(index) {
      this.changeIndex = index;
    },
    reset1() {
      this.isprice = false;
      this.isprice1 = false;
      this.isprice2 = false;
      this.isprice3 = false;
      this.isprice4 = false;
      this.isprice5 = false;
      this.isprice6 = false;
    },
    price0() {
      if (this.isprice == true) {
        this.isprice = false;
      } else {
        this.isprice = true;
      }
    },
    price1() {
      if (this.isprice1 == true) {
        this.isprice1 = false;
      } else {
        this.isprice1 = true;
      }
    },
    price2() {
      if (this.isprice2 == true) {
        this.isprice2 = false;
      } else {
        this.isprice2 = true;
      }
    },
    price3() {
      if (this.isprice3 == true) {
        this.isprice3 = false;
      } else {
        this.isprice3 = true;
      }
    },
    price4() {
      if (this.isprice4 == true) {
        this.isprice4 = false;
      } else {
        this.isprice4 = true;
      }
    },
    price5() {
      if (this.isprice5 == true) {
        this.isprice5 = false;
      } else {
        this.isprice5 = true;
      }
    },
    price6() {
      if (this.isprice6 == true) {
        this.isprice6 = false;
      } else {
        this.isprice6 = true;
      }
    },
    reset() {
      (this.ishrent = false),
        (this.ishrent1 = false),
        (this.ishrent2 = false),
        (this.ishrent3 = false),
        (this.ishrent4 = false),
        (this.isrent = false),
        (this.isrent1 = false),
        (this.isrent2 = false),
        (this.isrent3 = false),
        (this.isrent4 = false);
    },
    rent() {
      if (this.isrent == true) {
        this.isrent = false;
      } else {
        this.isrent = true;
        this.isrent1 = false;
        this.isrent2 = false;
        this.isrent3 = false;
        this.isrent4 = false;
      }
    },
    hrent() {
      if (this.ishrent == true) {
        this.ishrent = false;
      } else {
        this.ishrent = true;
        this.ishrent1 = false;
        this.ishrent2 = false;
        this.ishrent3 = false;
        this.ishrent4 = false;
      }
    },
    rent1() {
      if (this.isrent1 == true) {
        this.isrent1 = false;
      } else {
        this.isrent1 = true;
      }
    },
    hrent1() {
      if (this.ishrent1 == true) {
        this.ishrent1 = false;
      } else {
        this.ishrent1 = true;
      }
    },
    rent2() {
      if (this.isrent2 == true) {
        this.isrent2 = false;
      } else {
        this.isrent2 = true;
      }
    },
    hrent2() {
      if (this.ishrent2 == true) {
        this.ishrent2 = false;
      } else {
        this.ishrent2 = true;
      }
    },
    rent3() {
      if (this.isrent3 == true) {
        this.isrent3 = false;
      } else {
        this.isrent3 = true;
      }
    },
    hrent3() {
      if (this.ishrent3 == true) {
        this.ishrent3 = false;
      } else {
        this.ishrent3 = true;
      }
    },
    rent4() {
      if (this.isrent4 == true) {
        this.isrent4 = false;
      } else {
        this.isrent4 = true;
      }
    },
    hrent4() {
      if (this.ishrent4 == true) {
        this.ishrent4 = false;
      } else {
        this.ishrent4 = true;
      }
    },
    changeColor(e) {
      if (this.ischange == true) {
        this.ischange = false;

        e.currentTarget.style.color = "red";
      } else {
        this.ischange = true;
        e.currentTarget.style.color = "black";
      }
    },
    setEvent(e) {
      console.log(e.currentTarget.innerHTML);
      this.toLocation = e.currentTarget.innerHTML;
      e.currentTarget.style.color = "red";
    },
    pquyu(e) {
      this.isquyu = true;
      e.currentTarget.style.color = "red";
    },
    open() {
      if (this.isto == true) {
        this.isto = false;
        // e.currentTarget.style.color = "black";
      } else {
        this.isto = true;
        this.isto1 = false;
        // e.currentTarget.style.color = "red";
      }
    },
    open1(e) {
      if (this.isto1 == true) {
        this.isto1 = false;
        e.currentTarget.style.color = "black";
      } else {
        this.isto1 = true;
        this.isto = false;
        this.isto2 = false;
        this.isto3 = false;
        this.isto4 = false;
        e.currentTarget.style.color = "red";
      }
    },
    open2() {
      if (this.isto2 == true) {
        this.isto2 = false;
      } else {
        this.isto2 = true;
        this.isto = false;
        this.isto1 = false;
        this.isto3 = false;
        this.isto4 = false;
      }
    },
    open3() {
      if (this.isto3 == true) {
        this.isto3 = false;
      } else {
        this.isto3 = true;
        this.isto = false;
        this.isto1 = false;
        this.isto2 = false;
        this.isto4 = false;
      }
    },
    open4() {
      if (this.isto4 == true) {
        this.isto4 = false;
      } else {
        this.isto4 = true;
        this.isto = false;
        this.isto1 = false;
        this.isto2 = false;
        this.isto3 = false;
      }
    },
  },
  mounted(){
    this.loadNavItems();
  },
  computed: {
    //  ...mapState(['active'])
    activeNews: {
      get() {
        return this.$store.state.activeNews;
      },
      set(val) {},
    },
  },
};
</script>
<style>
/* 筛选模块 */
.Renting .filter {
  margin-bottom: 1rem;
}
/*搜索框 */
.Renting .search {
  position: fixed;
  top: 1px;
  width: 100%;
  z-index: 99;
}
.Renting .mint-searchbar {
  background-color: #fff;
}
.Renting .mint-searchbar-inner,
.mint-searchbar-core {
  background-color: rgb(245, 245, 245);
  border-radius: 2rem;
}
.Renting .mint-searchbar-inner {
  box-shadow: inset 0px 0px 3px -1px;
}

/* 搜索框结束 */
.Renting .content {
  height: 12rem;
}
/* 功能栏标签 */
.Renting .tagbtn1 {
  /* position: fixed; */
  bottom: 33rem;
  font-weight: 200;
  width: 90%;
  margin: auto;
  background-image: none;
}
.Renting .tagbtn2 {
  /* position: fixed; */
  bottom: 28rem;
  font-weight: 200;
  width: 90%;
  margin: auto;
  background-image: none;
}
.Renting .mint-tab-item-label {
  font-size: 0.8rem;
}
.Renting .mint-tab-item-icon {
  width: 3rem;
  height: 3rem;
}
/* 搜索标签 */
.Renting .mint-search {
  /* position: fixed; */
  width: 100%;
  top: 1px;
  z-index: 999;
  height: 4rem;
}
</style>
<style lang="less" scoped>
@background-color: #fff;
.Renting {
  .pupop {
    position: absolute;
    top: 270px;
    right: 0;
    z-index: 999;
    background-color:@background-color;
    width: 100%;
    height: 50%;
    overflow: hidden;
    display: flex;
    justify-content: space-around;
    ul:nth-child(2),
    ul:nth-child(3) {
      overflow: auto;
    }
    ul {
      list-style: none;
      padding: 20px;
      li {
        margin-bottom: 20px;
        margin-left: 15px;
      }
    }
  }
  .box {
    display: flex;
    justify-content: space-around;
    margin-top: 3rem;
    overflow: hidden;
    button {
      width: 22%;
      outline: 0;
      border: 0;
      background-color: #fff;
      overflow: hidden;
    }
  }
  .way {
    position: absolute;
    top: 270px;
    right: 0;
    z-index: 999;
    background-color: @background-color;
    width: 100%;
    height: 40%;
    h3 {
      margin: 30px 0 15px 21px;
    }
    div {
      display: flex;
      justify-content: space-around;
    }
    .bottom {
      button {
        height: 40px;
        width: 30%;
        outline: 0;
        border: 0;
      }
    }
  }
  .price {
    position: absolute;
    top: 270px;
    right: 0;
    z-index: 999;
    background-color: @background-color;
    width: 100%;
    height: 50%;

    .box {
      display: flex;
      flex-direction: column;
      justify-content: flex-start;
      align-items: center;
      margin: 0;
      div {
        width: 30%;
        height: 20px;
        margin: 10px 0;
        span {
          width: 30%;
          text-align: center;
        }
