<script>
export default {
  data() {
    return {
      isPopSet: false,
      ff: true,
      imgsrc: require("../assets/img/8.jpg"),
    };
  },
  methods: {
    linkHistory() {
      this.$router.push({ name: "history" });
    },
    linkCollect() {
      this.$router.push({ name: "collect" });
    },
    linkBill() {
      this.$router.push({ name: "bill" });
    },
    linkUserCenter() {
      this.$router.push({ name: "usercenter",params:{parent:"user"} });
      // console.log(1)
    },
    show() {
      if (this.ff == true) {
        this.ff = false;
      } else {
        this.ff = true;
      }
    },
    show2() {
      if (this.ff == true) {
        this.ff = false;
      } else {
        this.ff = true;
      }
    },

    popSet() {
      // 弹出设置页面
      this.isPopSet = !this.isPopSet;
    },

    // 退出登录
    logOut() {
      // 将用户数据清空
      this.$store.state.dataUser = "";
      sessionStorage.resultData = "";

      // 跳转界面回login
      this.$router.push({
        name: "login",
      });
    },
  },
};
</script>
