<template>
  <div class="comment">
    <div class="d1">
      <span style="margin-left: 10px">全部留言 ({{ comments.length }})</span>
      <span style="margin-left: 200px">赞 3</span>
      <div class="item" v-for="(item,i) in comments" :key="i">
        <div class="avatar">
          <img :src="require(`../assets/default_avatar.jpg`)" alt="" />
        </div>
        <div class="user" v-html="item.user_name"></div>
        <div class="more">...</div>
        <div class="content" v-html="item.comments"></div>
        <div class="time">{{ item.time }}</div>
        <mt-button class="btn" size="small">回复</mt-button>
      </div>
    </div>

    <div class="foot">
      <input type="text" placeholder="写留言" @focus="messageBox" />
      <div class="good">
        <van-icon name="good-job-o" />
        <!-- <img src="../../public/good.jpg" alt="" /> -->
        <span>点赞</span>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  props:{
    comments:{
      type: Array,
      required: true,
    }
  },
  data() {
    return {

    }
  },
  methods: {
    getTime: function () {
      let yy = new Date().getFullYear();
      var mm =
        new Date().getMonth() < 10
          ? "0" + (new Date().getMonth() + 1)
          : new Date().getMonth() + 1;
      var dd =
        new Date().getDate() < 10
          ? "0" + new Date().getDate()
          : new Date().getDate();
      let hh = new Date().getHours();
      let mf =
        new Date().getMinutes() < 10
          ? "0" + new Date().getMinutes()
          : new Date().getMinutes();
      let ss =
        new Date().getSeconds() < 10
          ? "0" + new Date().getSeconds()
          : new Date().getSeconds();
      var time = yy + "-" + mm + "-" + dd + " " + hh + ":" + mf + ":" + ss;
      return time;
    },
    messageBox() {
      this.$messagebox({
        $type: "prompt",
        message: " ",
        showInput: true,
        inputPlaceholder: "想对Ta说点什么",
        showConfirmButton: true,
        showCancelButton: true,
      })
        .then(({ value, action }) => {
          if(value){
            var time=this.getTime();
            this.axios.post('/addcomments',`rid=${this.comments[0].rid}&uid=${this.$store.state.dataUser.uid}&comments=${value}&time=${time}`).then((res)=>{
              if (res.data.code == 200){
                console.log('ok');
                location.reload();
              }
            })
          }else{
          }
        })
        .catch(() => {});
    },
  },
  mounted(){
  }
};
</script>
<style>
.comment .d1 {
  width: 362px;
  height: 500px;
  margin-top: 20px;
  margin-left: 7px;
  padding: 6px;
  padding: 10px;
  margin-bottom: 20px;
  background-color: #fff;
}
.comment .item {
  position: relative;
  margin-top: 40px;
  margin-bottom: 20px;
}
.comment .avatar img {
  width: 44px;
  height: 44px;
  border-radius: 50%;
  margin-left: 10px;
  background-repeat: no-repeat;
  background-size: contain;
}
.comment .user {
  max-width: 150px;
  font-size: 19px;
  position: absolute;
  top: 2px;
  left: 70px;
}
.comment .user span{
  font-size: 23px;
  font-weight: 600;
  margin-left: 10px;
}
.comment .more {
  position: absolute;
  right: 10px;
  top: -15px;
  font-size: 34px;
  margin-right: 10px;
}
.comment .content {
  margin-left: 70px;
  background-color:#FFF;
  padding: 10px 0;
  font-size: 20px;
  border-radius: 5px;
  
}
/* .comment .content .span{
  color: red;
} */
.comment .time {
  float: left;
  margin-left: 70px;
  margin-top: 10px;
  color: gray;
  font-size: 16px;
}
.comment .btn {
  float: left;
  margin-left: 10px;
  margin-top: 5px;
  margin-bottom: 2px;
}
.comment .foot {
  position: fixed;
  bottom: 0px;
  width: 100%;
  height: 50px;
  background-color: #fff;
}
.comment .foot input {
  margin-left: 5%;
  width: 70%;
  height: 30px;
  outline: none;
  border:0;
  background-color: rgb(235, 193, 235);
  border-radius: 18px;
  padding-left: 10px;
}
.comment .good {
  position: absolute;
  right: 25px;
  top: 3px;
}
.comment .good .van-icon {
  /* width: 25px;
  float: left; */
  margin-top: 10px;
  width: 10px;
}
.comment .good span {
  display: block;
  float: left;
  margin-top: 3px;
  font-size: 20px;
  color: red;
}

</style>