<template>
  <div class="collect">
      <Popup :popupVisible=true :back="back" :source="this.$store.state.collect">
          <main class="main">

    <ZiRoom @click.native="linkRoom"></ZiRoom>
          </main>
      </Popup>
  </div>
</template>
<script>
import Popup from './Popup.vue';
import ZiRoom from "./ZiRoom.vue";
export default {
  components: {
    ZiRoom,
    Popup,
  },
  data(){
      return{
          back:"user",
          collect:'收藏'
      }
  },
  methods: {
    linkRoom() {
      this.$router.push({ name: "roomDetails" });
    }
   
  },
  mounted() {
    this.$store.commit("saveCollect",this.collect)
  },
};
</script>
<style>
.collect .main {
    margin-top: 1.3rem;
}
</style>