<template>
  <div class="history" >
      <Popup :popupVisible=true :back="back" :source="this.$store.state.history">
          <main class="main">

    <ZiRoom @click.native="linkRoom"></ZiRoom>
          </main>
      </Popup>
  </div>
</template>
<script>
import Popup from './Popup.vue';
import ZiRoom from "./ZiRoom.vue";
export default {
  components: {
    ZiRoom,
    Popup,
  },
  data(){
      return{
          back:"user",
          history:'历史记录'
      }
  },
  methods: {
    linkRoom() {
      this.$router.push({ name: "roomDetails" });
    }
    
      
   
  },
  mounted() {
    this.$store.commit("saveHistory",this.history)
  },
};
</script>
<style>
.history .main {
    margin-top: 1.3rem;
}
</style>